  font-size: 24px;
  font-weight: 600;
  margin-bottom: 16px;
}

.modal-body {
  color: var(--text-secondary);
  line-height: 1.6;
  margin-bottom: 24px;
}

.modal-body strong {
  color: var(--primary);
  font-weight: 600;
}

.update-notes {
  margin-top: 12px;
  padding: 12px;
  background: var(--bg-input);
  border-radius: var(--radius-sm);
  font-size: 13px;
  max-height: 150px;
  overflow-y: auto;
}

.modal-footer {
  display: flex;
  gap: 12px;
  justify-content: flex-end;
}

