wght@300;400;500;600;700&display=swap');\n\n:root {\n  /* Renk Paleti - Derin Karanlık */\n  --bg-base: #050505;\n  --bg-sidebar: #0a0a0a;\n  --bg-surface: #121212;\n  --bg-surface-hover: #1e1e1e;\n  --bg-input: #181818;\n\n  --border-light: rgba(255, 255, 255, 0.08);\n  --border-hover: rgba(255, 255, 255, 0.15);\n\n  --primary: #ff0033;\n  --primary-dark: #cc0000;\n  --primary-gradient: linear-gradient(135deg, #ff0033 0%, #a80022 100%);\n  --primary-glow: 0 0 20px rgba(255, 0, 51, 0.3);\n  --primary-glow-hover: 0 0 30px rgba(255, 0, 51, 0.5);\n\n  --text-primary: #ffffff;\n  --text-secondary: #a1a1a1;\n  --text-muted: #666666;\n\n  --success: #10b981;\n  --error: #ef4444;\n\n  /* Animasyonlar */\n  --ease-spring: cubic-bezier(0.175, 0.885, 0.32, 1.275);\n  --ease-smooth: cubic-bezier(0.4, 0, 0.2, 1);\n  --duration-fast: 0.2s;\n  --duration-normal: 0.3s;\n\n  /* Boyutlar */\n  --radius-sm: 8px;\n  --radius-md: 14px;\n  --radius-lg: 24px;\n  --radius-pill: 9999px;\n\n  --sidebar-width: 260px;\n  --titlebar-height: 40px;\n\n  --bg-gradient: radial-gradient(circle at top right, #1a0505 0%, var(--bg-base) 40%);\n}\n\n/* Aydınlık Tema Override (İsteğe bağlı, ama karanlık odaklıyız) */\n[data-theme=\"light\"] {\n  --bg-base: #ffffff;\n  --bg-sidebar: #f5f5f7;\n  --bg-surface: #ffffff;\n  --bg-surface-hover: #f0f0f2;\n  --bg-input: #ffffff;\n  --border-light: rgba(0, 0, 0, 0.1);\n  --border-hover: rgba(0, 0, 0, 0.2);\n  --text-primary: #1d1d1f;\n  --text-secondary: #86868b;\n  --primary-glow: 0 0 20px rgba(255, 0, 51, 0.2);\n  --bg-gradient: radial-gradient(circle at top right, #ffecec 0%, var(--bg-base) 40%);\n}\n\n/* Reset */\n*,\n*::before,\n*::after {\n  margin: 0;\n  padding: 0;\n  box-sizing: border-box;\n}\n\nbody {\n  font-family: 'Outfit', 'Segoe UI', sans-serif;\n  background-color: var(--bg-base);\n  color: var(--text-primary);\n  height: 100vh;\n  overflow: hidden;\n  line-height: 1.6;\n  -webkit-font-smoothing: antialiased;\n}\n\n/* Scrollbar Tasarımı */\n::-webkit-scrollbar {\n  width: 6px;\n  height: 6px;\n}\n\n::-webkit-scrollbar-track {\n  background: transparent;\n}\n\n::-webkit-scrollbar-thumb {\n  background: var(--bg-surface-hover);\n  border-radius: var(--radius-pill);\n  transition: background var(--duration-fast);\n}\n\n::-webkit-scrollbar-thumb:hover {\n  background: var(--text-muted);\n}\n\n/* ============================================\n   Titlebar\n   ============================================ */\n.titlebar {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  height: var(--titlebar-height);\n  background: var(--bg-sidebar);\n  border-bottom: 1px solid var(--border-light);\n  border-bottom: 1px solid var(--border-light);\n  -webkit-app-region: drag;\n  user-select: none;\n  padding: 0 16px;\n  z-index: 100;\n}\n\n.titlebar-drag {\n  flex: 1;\n  display: flex;\n  align-items: center;\n}\n\n.app-logo {\n  display: flex;\n  align-items: center;\n  gap: 12px;\n  font-weight: 700;\n  font-size: 15px;\n  letter-spacing: 0.5px;\n  color: var(--text-primary);\n}\n\n.app-logo svg {\n  color: var(--primary);\n  filter: drop-shadow(0 0 8px rgba(255, 0, 51, 0.4));\n}\n\n.titlebar-controls {\n  -webkit-app-region: no-drag;\n  display: flex;\n  gap: 8px;\n}\n\n.titlebar-btn {\n  width: 32px;\n  height: 32px;\n  border-radius: var(--radius-sm);\n  border: none;\n  background: transparent;\n  color: var(--text-secondary);\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  cursor: pointer;\n  transition: all var(--duration-fast);\n}\n\n.titlebar-btn:hover {\n  background: var(--bg-surface-hover);\n  color: var(--text-primary);\n}\n\n.titlebar-btn-close:hover {\n  background: var(--error);\n  color: white;\n}\n\n/* ============================================\n   Layout\n   ============================================ */\n.app-container {\n  display: flex;\n  height: calc(100vh - var(--titlebar-height));\n  background: var(--bg-gradient);\n}\n\n/* Sidebar */\n.sidebar {\n  width: var(--sidebar-width);\n  width: var(--sidebar-width);\n  background: var(--bg-sidebar);\n  backdrop-filter: blur(20px);\n  backdrop-filter: blur(20px);\n  border-right: 1px solid var(--border-light);\n  padding: 24px 16px;\n  display: flex;\n  flex-direction: column;\n  gap: 8px;\n}\n\n.nav-btn {\n  display: flex;\n  align-items: center;\n  gap: 14px;\n  padding: 14px 20px;\n  border: 1px solid transparent;\n  background: transparent;\n  color: var(--text-secondary);\n  font-size: 15px;\n  font-weight: 500;\n  border-radius: var(--radius-md);\n  cursor: pointer;\n  transition: all var(--duration-normal) var(--ease-spring);\n  position: relative;\n  overflow: hidden;\n}\n\n.nav-btn svg {\n  transition: transform var(--duration-normal) var(--ease-spring);\n}\n\n.nav-btn:hover {\n  background: var(--bg-surface-hover);\n  color: var(--text-primary);\n  padding-left: 24px;\n}\n\n.nav-btn:hover svg {\n  transform: scale(1.1);\n}\n\n.nav-btn.active {\n  background: rgba(255, 0, 51, 0.1);\n  color: var(--primary);\n  font-weight: 600;\n  border-color: rgba(255, 0, 51, 0.15);\n}\n\n.nav-btn.active::before {\n  content: '';\n  position: absolute;\n  left: 0;\n  top: 50%;\n  transform: translateY(-50%);\n  width: 4px;\n  height: 20px;\n  background: var(--primary);\n  border-radius: 0 var(--radius-pill) var(--radius-pill) 0;\n  box-shadow: 0 0 10px var(--primary);\n}\n\n.sidebar-footer {\n  margin-top: auto;\n  padding-top: 20px;\n  border-top: 1px solid var(--border-light);\n}\n\n.theme-btn {\n  width: 100%;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  gap: 10px;\n  padding: 14px;\n  background: var(--bg-surface);\n  border: 1px solid var(--border-light);\n  color: var(--text-secondary);\n  border-radius: var(--radius-md);\n  cursor: pointer;\n  transition: all var(--duration-fast);\n}\n\n.theme-btn:hover {\n  background: var(--bg-surface-hover);\n  border-color: var(--border-hover);\n  color: var(--text-primary);\n}\n\n/* Main Content */\n.main-content {\n  flex: 1;\n  padding: 40px;\n  overflow-y: auto;\n  position: relative;\n}\n\n/* Sayfa Geçişleri */\n.page {\n  display: none;\n  animation: slideUpFade 0.4s var(--ease-smooth);\n}\n\n.page.active {\n  display: block;\n}\n\n@keyframes slideUpFade {\n  from {\n    opacity: 0;\n    transform: translateY(20px);\n  }\n\n  to {\n    opacity: 1;\n    transform: translateY(0);\n  }\n}\n\n/* ============================================\n   Components\n   ============================================ */\n\n/* Headers */\n.page-header {\n  margin-bottom: 40px;\n  text-align: center;\n}\n\n.page-header h1 {\n  font-size: 32px;\n  font-weight: 700;\n  background: linear-gradient(to right, #fff, #aaa);\n  background-clip: text;\n  -webkit-background-clip: text;\n  -webkit-text-fill-color: transparent;\n  margin-bottom: 8px;\n  letter-spacing: -0.5px;\n}\n\n.page-header p {\n  color: var(--text-secondary);\n  font-size: 16px;\n}\n\n/* URL Input - Hero Section */\n.url-input-container {\n  margin-bottom: 40px;\n  position: relative;\n  z-index: 10;\n}\n\n.url-input-wrapper {\n  display: flex;\n  align-items: center;\n  gap: 16px;\n  padding: 8px 8px 8px 24px;\n  background: var(--bg-surface);\n  border: 1px solid var(--border-light);\n  border-radius: var(--radius-pill);\n  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.2);\n  transition: all 0.3s var(--ease-spring);\n}\n\n.url-input-wrapper:focus-within {\n  transform: translateY(-2px);\n  border-color: var(--primary);\n  box-shadow: 0 15px 50px rgba(255, 0, 51, 0.15), 0 0 0 2px rgba(255, 0, 51, 0.1);\n}\n\n.url-icon {\n  color: var(--text-muted);\n  transition: color var(--duration-fast);\n}\n\n.url-input-wrapper:focus-within .url-icon {\n  color: var(--primary);\n}\n\n#url-input {\n  flex: 1;\n  background: transparent;\n  border: none;\n  color: var(--text-primary);\n  font-size: 16px;\n  font-weight: 500;\n  font-family: inherit;\n  outline: none;\n}\n\n#url-input::placeholder {\n  color: var(--text-muted);\n}\n\n/* Primary Button */\n.btn-primary {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  padding: 14px 36px;\n  background: var(--primary-gradient);\n  color: white;\n  border: none;\n  border-radius: var(--radius-pill);\n  font-size: 16px;\n  font-weight: 600;\n  cursor: pointer;\n  transition: all var(--duration-fast);\n  box-shadow: var(--primary-glow);\n  position: relative;\n  overflow: hidden;\n}\n\n.btn-primary::before {\n  content: '';\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  background: linear-gradient(rgba(255, 255, 255, 0.2), transparent);\n  opacity: 0;\n  transition: opacity var(--duration-fast);\n}\n\n.btn-primary:hover {\n  transform: translateY(-2px) scale(1.02);\n  box-shadow: var(--primary-glow-hover);\n}\n\n.btn-primary:hover::before {\n  opacity: 1;\n}\n\n.btn-primary:active {\n  transform: translateY(0) scale(0.98);\n}\n\n.btn-loading {\n  color: transparent !important;\n  pointer-events: none;\n  position: relative;\n}\n\n.btn-loading::after {\n  content: '';\n  position: absolute;\n  left: 50%;\n  top: 50%;\n  width: 20px;\n  height: 20px;\n  margin-left: -10px;\n  margin-top: -10px;\n  border: 2px solid rgba(255, 255, 255, 0.3);\n  border-top-color: white;\n  border-radius: 50%;\n  animation: btn-spinner 0.6s linear infinite;\n}\n\n@keyframes btn-spinner {\n  to {\n    transform: rotate(360deg);\n  }\n}\n\n/* Secondary Button */\n.btn-secondary {\n  display: inline-flex;\n  align-items: center;\n  gap: 10px;\n  padding: 10px 20px;\n  background: var(--bg-surface);\n  border: 1px solid var(--border-light);\n  color: var(--text-secondary);\n  border-radius: var(--radius-md);\n  font-size: 14px;\n  font-weight: 500;\n  cursor: pointer;\n  transition: all var(--duration-fast);\n  text-decoration: none;\n}\n\n.btn-secondary:hover {\n  background: var(--bg-surface-hover);\n  border-color: var(--border-hover);\n  color: var(--text-primary);\n  transform: translateY(-1px);\n}\n\n/* Cards */\n.video-info,\n.playlist-info,\n.download-progress,\n.download-complete,\n.settings-item {\n  background: var(--bg-surface);\n  border: 1px solid var(--border-light);\n  border-radius: var(--radius-lg);\n  padding: 24px;\n  transition: transform var(--duration-normal), box-shadow var(--duration-normal);\n}\n\n.download-complete {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  text-align: center;\n  gap: 16px;\n}\n\n.download-complete h2 {\n  font-size: 24px;\n  margin-bottom: 8px;\n}\n\n.complete-actions {\n  display: flex;\n  gap: 12px;\n  justify-content: center;\n  margin-top: 10px;\n}\n\n.video-info:hover,\n.playlist-info:hover {\n  border-color: var(--border-hover);\n  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);\n}\n\n/* Video Info Spesifik */\n.video-info {\n  display: grid;\n  grid-template-columns: 360px 1fr;\n  gap: 30px;\n  margin-bottom: 24px;\n  padding: 30px;\n}\n\n.video-thumbnail-container {\n  border-radius: var(--radius-md);\n  overflow: hidden;\n  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.5);\n  position: relative;\n  display: flex;\n}\n\n.video-thumbnail-container::after {\n  content: '';\n  position: absolute;\n  inset: 0;\n  box-shadow: inset 0 0 20px rgba(0, 0, 0, 0.5);\n  pointer-events: none;\n}\n\n#video-thumbnail {\n  width: 100%;\n  aspect-ratio: 16/9;\n  object-fit: cover;\n  display: block;\n  transition: transform 0.5s ease;\n}\n\n.video-info:hover #video-thumbnail {\n  transform: scale(1.05);\n}\n\n.video-details h2 {\n  font-size: 20px;\n  font-weight: 600;\n  line-height: 1.4;\n  margin-bottom: 12px;\n}\n\n.video-meta {\n  display: inline-flex;\n  align-items: center;\n  gap: 10px;\n  padding: 6px 12px;\n  background: var(--bg-surface-hover);\n  border-radius: var(--radius-pill);\n  font-size: 13px;\n  color: var(--text-secondary);\n  margin-bottom: 24px;\n}\n\n/* Form Elements */\n.option-group label:first-child {\n  font-size: 13px;\n  font-weight: 600;\n  color: var(--text-muted);\n  text-transform: uppercase;\n  letter-spacing: 0.5px;\n  margin-bottom: 10px;\n  display: block;\n}\n\n.radio-group {\n  display: flex;\n  gap: 16px;\n  background: var(--bg-base);\n  padding: 6px;\n  border-radius: var(--radius-md);\n  width: fit-content;\n}\n\n.radio-option {\n  position: relative;\n  cursor: pointer;\n}\n\n.radio-option input {\n  display: none;\n}\n\n/* Custom Radio Button yerine daha modern \"Segmented Control\" benzeri yapı */\n.radio-option {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n  padding: 8px 16px;\n  border-radius: var(--radius-sm);\n  transition: all var(--duration-fast);\n  color: var(--text-secondary);\n}\n\n.radio-option .radio-custom {\n  /* Eski yuvarlak stili gizle veya değiştir.\n     Burada tamamen yeni bir stil uygulayalım.\n  */\n  display: none;\n}\n\n.radio-option:has(input:checked) {\n  background: var(--bg-surface-hover);\n  color: var(--primary);\n  font-weight: 500;\n}\n\n/* Select Box */\n#quality-select {\n  width: 100%;\n  padding: 12px 16px;\n  background: var(--bg-base);\n  border: 1px solid var(--border-light);\n  color: var(--text-primary);\n  border-radius: var(--radius-md);\n  outline: none;\n  cursor: pointer;\n  transition: all var(--duration-fast);\n}\n\n#quality-select:hover {\n  border-color: var(--border-hover);\n}\n\n#quality-select:focus {\n  border-color: var(--primary);\n}\n\n/* Download Action Button */\n.btn-download {\n  width: 100%;\n  justify-content: center;\n  margin-top: auto;\n  padding: 16px;\n  font-size: 16px;\n  background: var(--primary-gradient);\n  color: white;\n  border: none;\n  border-radius: var(--radius-md);\n  font-weight: 600;\n  cursor: pointer;\n  transition: all 0.3s var(--ease-spring);\n  box-shadow: var(--primary-glow);\n  display: flex;\n  align-items: center;\n  gap: 10px;\n}\n\n.btn-download:hover {\n  transform: translateY(-2px);\n  box-shadow: var(--primary-glow-hover);\n}\n\n/* Progress Bar */\n.progress-bar {\n  height: 10px;\n  background: var(--bg-base);\n  border-radius: var(--radius-pill);\n  overflow: hidden;\n  position: relative;\n}\n\n.progress-fill {\n  background: var(--primary-gradient);\n  box-shadow: 0 0 10px var(--primary);\n  height: 100%;\n  border-radius: var(--radius-pill);\n  position: relative;\n  overflow: hidden;\n}\n\n.progress-fill::after {\n  content: '';\n  position: absolute;\n  top: 0;\n  left: 0;\n  bottom: 0;\n  right: 0;\n  background: linear-gradient(45deg,\n      rgba(255, 255, 255, 0.15) 25%,\n      transparent 25%,\n      transparent 50%,\n      rgba(255, 255, 255, 0.15) 50%,\n      rgba(255, 255, 255, 0.15) 75%,\n      transparent 75%,\n      transparent);\n  background-size: 20px 20px;\n  animation: moveStripes 1s linear infinite;\n}\n\n@keyframes moveStripes {\n  0% {\n    background-position: 0 0;\n  }\n\n  100% {\n    background-position: 20px 20px;\n  }\n}\n\n/* History List */\n.history-list {\n  display: grid;\n  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));\n  gap: 20px;\n}\n\n.history-item {\n  background: var(--bg-surface);\n  border: 1px solid var(--border-light);\n  border-radius: var(--radius-md);\n  padding: 16px;\n  display: flex;\n  gap: 16px;\n  transition: all var(--duration-fast);\n}\n\n.history-item:hover {\n  border-color: var(--border-hover);\n  transform: translateY(-2px);\n  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);\n}\n\n.history-item img {\n  width: 100px;\n  height: 56px;\n  object-fit: cover;\n  border-radius: var(--radius-sm);\n}\n\n.history-item-info h4 {\n  font-size: 14px;\n  font-weight: 500;\n  margin-bottom: 4px;\n  display: -webkit-box;\n  line-clamp: 2;\n  -webkit-line-clamp: 2;\n  -webkit-box-orient: vertical;\n  overflow: hidden;\n}\n\n.history-item-actions {\n  display: flex;\n  flex-direction: column;\n  gap: 8px;\n  justify-content: center;\n}\n\n.history-item-actions button {\n  padding: 6px 12px;\n  font-size: 12px;\n  border-radius: var(--radius-sm);\n  background: var(--bg-base);\n  border: 1px solid var(--border-light);\n  color: var(--text-secondary);\n  cursor: pointer;\n  transition: all var(--duration-fast);\n}\n\n.history-item-actions button:hover {\n  background: var(--bg-surface-hover);\n  color: var(--text-primary);\n}\n\n.settings-list {\n  display: flex;\n  flex-direction: column;\n  gap: 16px;\n}\n\n.settings-item {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  padding: 24px;\n}\n\n.settings-info h3 {\n  font-size: 16px;\n  font-weight: 500;\n  margin-bottom: 4px;\n}\n\n.hidden {\n  display: none !important;\n}\n\n.app-footer {\n  margin-top: 40px;\n  text-align: center;\n  font-size: 12px;\n  color: var(--text-muted);\n  opacity: 0.7;\n  padding-bottom: 20px;\n}\n\n/* ============================================\n   Modal Styles\n   ============================================ */\n\n.modal {\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  z-index: 1000;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n\n.modal-overlay {\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  background: rgba(0, 0, 0, 0.8);\n  backdrop-filter: blur(5px);\n}\n\n.modal-content {\n  position: relative;\n  background: var(--bg-surface);\n  border: 1px solid var(--border-light);\n  border-radius: var(--radius-lg);\n  max-width: 450px;\n  width: 90%;\n  padding: 32px;\n  animation: modalSlideIn 0.3s var(--ease-spring);\n}\n\n@keyframes modalSlideIn {\n  from {\n    opacity: 0;\n    transform: scale(0.9) translateY(-20px);\n  }\n  to {\n    opacity: 1;\n    transform: scale(1) translateY(0);\n  }\n}\n\n.modal-header h2 {\n  font-size: 24px;\n  font-weight: 600;\n  margin-bottom: 16px;\n}\n\n.modal-body {\n  color: var(--text-secondary);\n  line-height: 1.6;\n  margin-bottom: 24px;\n}\n\n.modal-body strong {\n  color: var(--primary);\n  font-weight: 600;\n}\n\n.update-notes {\n  margin-top: 12px;\n  padding: 12px;\n  background: var(--bg-input);\n  border-radius: var(--radius-sm);\n  font-size: 13px;\n  max-height: 150px;\n  overflow-y: auto;\n}\n\n.modal-footer {\n  display: flex;\n  gap: 12px;\n  justify-content: flex-end;\n}\n\n.modal-footer .btn-primary {\n  padding: 12px 24px;\n}"],"sourceRoot":""}]);
